.GalaxyList {
  display: grid;
  grid-template-columns:  repeat(12, 1fr);;
  gap: 1rem;
  padding: 0 3rem 3rem;
  align-content:start;

}
/* Media query para pantallas grandes */
@media screen and (max-width: 1199px) {
  .GalaxyList {
    grid-template-columns: repeat(10, 1fr); /* Reducimos a 10 columnas */
  }
}

/* Media query para pantallas medianas */
@media screen and (max-width: 991px) {
  .GalaxyList {
    grid-template-columns: repeat(8, 1fr); /* Reducimos a 8 columnas */
  }
}

/* Media query para pantallas pequeñas */
@media screen and (max-width: 768px) {
  .GalaxyList {
    grid-template-columns: repeat(4, 1fr); /* Reducimos a 6 columnas */
  }
}

/* Media query para pantallas extra pequeñas */
@media screen and (max-width: 480px) {
  .GalaxyList {
    grid-template-columns: repeat(1, 1fr); /* Reducimos a 4 columnas */
  }
}
.Item {
  grid-column: span 4; /* Este elemento ocupa 3 columnas */
}
/* Media query para pantallas medianas */
@media screen and (max-width: 991px) {
  .Item {
    grid-column: span 3; /* Este elemento ocupa 2 columnas */
  }
}
/* Media query para pantallas más pequeñas */
@media screen and (max-width: 768px) {
  .Item {
    grid-column: span 2; /* Este elemento ocupa 2 columnas */
  }
}

/* Media query para pantallas aún más pequeñas */
@media screen and (max-width: 480px) {
  .Item {
    grid-column: span 1;
    /* Este elemento ocupa 1 columna */
  }
}


